<template>
  <div>
    <div class="intensity">
      <div class="intensity_txt">
        <img src="../assets/images/qltj.png" alt />
      </div>

      <ul class="intensity_list">
        <li v-for="(item, index) in rmtjlist" :key="index">
          <router-link :to="item.link">
            <span class="img-box">
              <img :src="item.img" />
            </span>
            <span class="zp-name">{{ item.tit }}</span>
            <p class="zp-txt">{{item.txt}}</p>
          </router-link>
        </li>
      </ul>
    </div>

    <div class="zp-content">
      <div class="zp-title">
        <div class="zp-title-str">{{ toptit.title }}</div>
          <span class="comic-more" @click="skip">更多 <van-icon name="arrow" /></span>
      </div>
      <ul class="comic-list-other">
        <li v-for="(v, i) in toplist" :key="i">
          <router-link :to="v.link">
            <span class="img-box">
              <img :src="v.img" />
            </span>
            <span class="zp-name">{{ v.tit }}</span>
             <p class="zp-txt">{{v.txt}}</p>
          </router-link>
        </li>
      </ul>
    </div>

    <div class="zp-content">
      <div class="zp-title">
        <div class="zp-title-str">{{ bottomtit.title }}</div>
        <span class="comic-more" @click="skipnew">更多<van-icon name="arrow" /></span>
      </div>
      <ul class="comic-list-other">
        <li v-for="(t, i) in bottomlist" :key="i">
          <router-link :to="t.link">
            <span class="img-box">
              <img :src="t.img" />
            </span>
            <span class="zp-name">{{ t.tit }}</span>
             <p class="zp-txt">{{t.txt}}</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  props:{
    rmtjlist :Array,
    toptit :Object,
    toplist :Array,
    bottomtit :Object,
    bottomlist :Array,
  },
  methods:{
    skip(){
       this.$router.push({ path: "/hot",  query: { typeid: 'hot'}});
    },
     skipnew(){
       this.$router.push({ path: "/hot",  query: { typeid: 'dalu'}});
    }
  }
};
</script>

